import { Component, computed, signal } from '@angular/core';
import { NgClass, NgFor, NgIf } from '@angular/common';
import { RouterLink } from '@angular/router';
import { ToolService } from '../../services/tool.service';

@Component({
  standalone: true,
  selector: 'app-compare-now',
  imports: [NgFor, NgIf, RouterLink,NgClass],
  styles: [`
    @keyframes floatUp { from { transform: translateY(6px); opacity: .85 } to { transform: translateY(0); opacity: 1 } }
    .animate-float { animation: floatUp .35s ease-out both }
    .glass { backdrop-filter: blur(12px); background: linear-gradient(180deg, rgba(255,255,255,.85), rgba(255,255,255,.65)); }
    .chip { @apply text-xs font-semibold px-2 py-1 rounded-full whitespace-nowrap; }
    .chip-brand { @apply bg-brand-50 text-brand-700 ring-1 ring-brand-200; }
    .chip-green { @apply bg-green-50 text-green-700 ring-1 ring-green-200; }
    .chip-yellow { @apply bg-yellow-50 text-yellow-700 ring-1 ring-yellow-200; }
    .chip-red { @apply bg-red-50 text-red-700 ring-1 ring-red-200; }
    .star { @apply text-lg leading-none; }
    .star--on { @apply text-amber-400; }
    .star--off { @apply text-slate-300; }
    .kpi-bar { @apply h-2 rounded-full bg-slate-200 overflow-hidden; }
    .kpi-bar > span { @apply block h-full bg-brand-500; }
    .rail { scroll-snap-type: x mandatory; -webkit-overflow-scrolling: touch; }
    .rail > * { scroll-snap-align: start; }
    .btn-toggle { @apply inline-flex items-center gap-2 rounded-2xl px-3 py-1.5 text-sm ring-1 transition; }
    .btn-toggle-on { @apply bg-brand-600 text-white ring-brand-600; }
    .btn-toggle-off { @apply bg-white text-slate-700 ring-slate-200 hover:bg-slate-50; }
    .table-cell { @apply p-3 align-top; }
    .table-head { @apply sticky left-0 bg-slate-50 font-semibold border-r border-slate-200; }
    .remove-chip { @apply text-xs px-2 py-1 rounded-full bg-slate-100 text-slate-600 hover:bg-red-50 hover:text-red-700 transition; }
    .card-hero { @apply bg-white rounded-2xl shadow-xl border border-slate-200 p-6 relative; }
  `],
  template: `
    <h1 class="text-4xl md:text-5xl font-black mb-3 text-center tracking-tight">
      <span class="bg-clip-text text-transparent bg-gradient-to-r from-brand-600 via-brand-500 to-brand-700">
        Compare AI Tools
      </span>
    </h1>
    <p class="max-w-3xl mx-auto mb-6 text-center text-slate-600 text-lg">
      Scan differences in a table, then dive deeper in card view. Toggle any time.
    </p>

    <!-- Toolbar -->
    <div class="sticky top-0 z-30 glass border-b border-slate-200">
      <div class="max-w-7xl mx-auto px-4 py-3 flex items-center justify-between gap-3">
        <div class="flex items-center gap-2 overflow-x-auto">
          <div *ngFor="let t of tools()" class="flex items-center gap-2 bg-white ring-1 ring-slate-200 rounded-xl px-2 py-1">
            <img [src]="t.logo" [alt]="t.name" class="h-6 w-6 rounded-md object-contain ring-1 ring-slate-200 bg-white" />
            <span class="text-sm font-semibold text-slate-800">{{ t.name }}</span>
            <button class="remove-chip" (click)="remove(t.id)" aria-label="Remove from compare">Remove</button>
          </div>
          <span *ngIf="!tools().length" class="text-sm text-slate-500">No tools selected yet.</span>
        </div>

        <div class="shrink-0 flex items-center gap-2">
    <button class="btn-toggle" [ngClass]="view() === 'table' ? 'btn-toggle-on' : 'btn-toggle-off'"
      (click)="view.set('table')" [attr.aria-pressed]="view()==='table'">Table</button>
    <button class="btn-toggle" [ngClass]="view() === 'cards' ? 'btn-toggle-on' : 'btn-toggle-off'"
      (click)="view.set('cards')" [attr.aria-pressed]="view()==='cards'">Cards</button>
          <a routerLink="/tools" class="btn-primary">Add More</a>
        </div>
      </div>
    </div>

    <!-- Empty state -->
    <div *ngIf="tools().length === 0" class="card text-center py-12 animate-float mt-6">
      <div class="text-2xl font-semibold mb-2">Nothing to compare yet</div>
      <div class="text-slate-500">Add up to 4 tools to see instant differences.</div>
      <a routerLink="/tools" class="btn-primary mt-6 inline-flex">Browse Tools</a>
    </div>

    <!-- TABLE VIEW -->
    <section *ngIf="tools().length && view() === 'table'" class="mt-6 animate-float">
      <div class="overflow-x-auto">
        <table class="min-w-[800px] w-full text-sm border-separate border-spacing-0">
          <thead>
            <tr class="text-left">
              <th class="table-cell table-head bg-slate-100 rounded-tl-xl">Metric</th>
              <th *ngFor="let t of tools()" class="table-cell font-semibold text-slate-700 bg-slate-100">
                <div class="flex items-center gap-2">
                  <img [src]="t.logo" [alt]="t.name" class="h-6 w-6 rounded-md ring-1 ring-slate-200 bg-white object-contain">
                  <a [routerLink]="['/tool', t.slug]" class="hover:underline text-slate-900">{{t.name}}</a>
                </div>
              </th>
            </tr>
          </thead>
          <tbody class="divide-y divide-slate-200">
            <!-- Category -->
            <tr>
              <td class="table-cell table-head">Category</td>
              <td *ngFor="let t of tools()" class="table-cell">{{t.category}}</td>
            </tr>
            <!-- Pricing -->
            <tr>
              <td class="table-cell table-head">Pricing</td>
              <td *ngFor="let t of tools()" class="table-cell">
                <span class="chip"
                      [ngClass]="t.pricing === 'Free' ? 'chip-green' : t.pricing === 'Paid' ? 'chip-red' : 'chip-yellow'">
                  {{t.pricing}}
                </span>
                <span class="text-slate-500" *ngIf="t.priceRange"> • {{t.priceRange}}</span>
              </td>
            </tr>
            <!-- Rating (stars + bar) -->
            <tr>
              <td class="table-cell table-head">Rating</td>
              <td *ngFor="let t of tools()" class="table-cell">
                <span class="mr-2">
                  <ng-container *ngFor="let s of stars">
                    <span class="star" [class.star--on]="s <= t.rating" [class.star--off]="s > t.rating">★</span>
                  </ng-container>
                </span>
                <span class="text-slate-600">{{t.rating}}/5</span>
                <div class="kpi-bar mt-2 w-48 max-w-full"><span [style.width.%]="(t.rating/5)*100"></span></div>
              </td>
            </tr>
            <!-- API -->
            <tr>
              <td class="table-cell table-head">API</td>
              <td *ngFor="let t of tools()" class="table-cell">
                <span class="chip" [ngClass]="t.apiAvailable ? 'chip-green' : 'chip-red'">
                  {{t.apiAvailable ? 'Available' : 'No API'}}
                </span>
              </td>
            </tr>
            <!-- Free Trial -->
            <tr>
              <td class="table-cell table-head">Free Trial</td>
              <td *ngFor="let t of tools()" class="table-cell">
                <span class="chip" [ngClass]="t.hasFreeTrial ? 'chip-green' : 'chip-red'">
                  {{t.hasFreeTrial ? 'Yes' : 'No'}}
                </span>
              </td>
            </tr>
            <!-- Best for / Tags -->
            <tr>
              <td class="table-cell table-head">Best for</td>
              <td *ngFor="let t of tools()" class="table-cell">
                <div class="flex flex-wrap gap-2">
                  <span *ngFor="let tag of t.tags" class="chip chip-brand">{{tag}}</span>
                  <span *ngIf="!t.tags?.length" class="text-slate-400">—</span>
                </div>
              </td>
            </tr>
            <!-- Pros -->
            <tr>
              <td class="table-cell table-head">Pros</td>
              <td *ngFor="let t of tools()" class="table-cell">
                <ul class="text-green-700 list-disc list-inside space-y-1">
                  <li *ngFor="let p of (t.pros | slice:0:3)">{{p}}</li>
                </ul>
              </td>
            </tr>
            <!-- Cons -->
            <tr>
              <td class="table-cell table-head rounded-bl-xl">Cons</td>
              <td *ngFor="let t of tools()" class="table-cell">
                <ul class="text-red-700 list-disc list-inside space-y-1">
                  <li *ngFor="let c of (t.cons | slice:0:3)">{{c}}</li>
                </ul>
              </td>
            </tr>
            <!-- CTA -->
            <tr class="bg-slate-50">
              <td class="table-cell table-head">—</td>
              <td *ngFor="let t of tools()" class="table-cell">
                <div class="flex items-center gap-2">
                  <a [routerLink]="['/tool', t.slug]" class="btn-secondary">Full Breakdown</a>
                  <a [href]="t.website" target="_blank" rel="noopener" class="btn-primary">Visit Site</a>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <p class="mt-4 text-xs text-slate-500 text-center">Tip: Scroll horizontally to view all columns on smaller screens.</p>
    </section>

    <!-- CARD VIEW -->
    <section *ngIf="tools().length && view() === 'cards'" class="mt-6 animate-float">
      <div class="rail flex gap-6 md:grid md:grid-cols-2 lg:grid-cols-{{cols()}} md:gap-8 justify-center">
        <article *ngFor="let t of tools()" class="card-hero">
          <div class="absolute -top-3 left-4 chip chip-brand">In Compare</div>

          <header class="flex items-center gap-3">
            <img [src]="t.logo" [alt]="t.name"
                 class="h-12 w-12 object-contain rounded-xl ring-2 ring-brand-200 bg-white" />
            <div class="min-w-0">
              <a [routerLink]="['/tool', t.slug]" class="font-extrabold text-xl text-slate-900 hover:text-brand-700 hover:underline truncate">
                {{t.name}}
              </a>
              <div class="text-xs text-slate-500">{{t.category}} • {{t.pricing}} <span *ngIf="t.priceRange">({{t.priceRange}})</span></div>
            </div>
            <button class="ml-auto remove-chip" (click)="remove(t.id)" aria-label="Remove from compare">Remove</button>
          </header>

          <section class="mt-4">
            <div class="flex items-center gap-2">
              <ng-container *ngFor="let s of stars">
                <span class="star" [class.star--on]="s <= t.rating" [class.star--off]="s > t.rating">★</span>
              </ng-container>
              <span class="text-sm text-slate-500">{{t.rating}}/5</span>
            </div>
            <div class="kpi-bar mt-3"><span [style.width.%]="(t.rating/5)*100"></span></div>
          </section>

          <section class="mt-3">
            <div class="text-sm text-brand-700 font-semibold bg-brand-50 ring-1 ring-brand-100 rounded-xl px-3 py-1 inline-block">
              Best for: <span class="font-bold">{{ (t.tags || []).join(', ') || 'General use' }}</span>
            </div>
          </section>

          <p class="mt-3 text-sm text-slate-700">{{t.shortDescription}}</p>

          <section class="mt-4">
            <div class="font-semibold text-slate-800 mb-2">Key Features</div>
            <ul class="space-y-1 text-sm">
              <li class="flex items-center gap-2">
                <span class="inline-flex h-5 w-5 items-center justify-center rounded-full"
                      [ngClass]="t.apiAvailable ? 'bg-green-100 text-green-700' : 'bg-red-100 text-red-700'">
                  {{ t.apiAvailable ? '✓' : '✗' }}
                </span>
                API Access
              </li>
              <li class="flex items-center gap-2">
                <span class="inline-flex h-5 w-5 items-center justify-center rounded-full"
                      [ngClass]="t.hasFreeTrial ? 'bg-green-100 text-green-700' : 'bg-red-100 text-red-700'">
                  {{ t.hasFreeTrial ? '✓' : '✗' }}
                </span>
                Free Trial
              </li>
              <li class="flex items-center gap-2">
                <span class="inline-flex h-5 w-5 items-center justify-center rounded-full"
                      [ngClass]="t.rating >= 4 ? 'bg-green-100 text-green-700' : 'bg-red-100 text-red-700'">
                  {{ t.rating >= 4 ? '✓' : '✗' }}
                </span>
                Highly Rated
              </li>
            </ul>
          </section>

          <section class="mt-4 grid grid-cols-2 gap-4">
            <div>
              <div class="font-semibold text-slate-800 mb-1">👍 Pros</div>
              <ul class="list-disc list-inside text-green-700 space-y-1 text-sm">
                <li *ngFor="let p of t.pros">{{p}}</li>
              </ul>
            </div>
            <div>
              <div class="font-semibold text-slate-800 mb-1">👎 Cons</div>
              <ul class="list-disc list-inside text-red-700 space-y-1 text-sm">
                <li *ngFor="let c of t.cons">{{c}}</li>
              </ul>
            </div>
          </section>

          <footer class="mt-6 flex items-center justify-between gap-3">
            <a [routerLink]="['/tool', t.slug]" class="btn-secondary">Full Breakdown</a>
            <a class="btn-primary" target="_blank" rel="noopener" [href]="t.website">Visit Official Site</a>
          </footer>
        </article>
      </div>
    </section>
  `
})
export class CompareNowComponent {
  view = signal<'table' | 'cards'>('table');
  stars = [1,2,3,4,5];

  tools = computed(() => {
    const ids = Array.from(this.svc.compareList());
    return this.svc.tools().filter(t => ids.includes(t.id));
  });

  cols = computed(() => Math.min(4, Math.max(1, this.tools().length || 1)));

  constructor(public svc: ToolService) {}

  remove(id: string) { this.svc.toggleCompare(id); }
}
